
import { useStore } from "@/store";
import { computed, defineComponent, PropType } from "vue";

export default defineComponent({
  name: "Input",
  props: {
    placeholder: {
      type: String,
      required: true,
    },
    typeValue: {
      type: String,
      default: "text",
    },
    error: {
      type: String,
    },
    value: {
      type: String,
      required: true,
    },
    onInput: {
      type: Function,
      required: true,
    },
    required: {
      type: Boolean,
      default: false,
    },
    style: {
      type: String as PropType<"icon" | "icon-alt" | "label">,
      default: "icon",
    },
  },
  setup() {
    const store = useStore();

    const theme = computed(() => store.state.settings.theme);

    return {
      theme,
    };
  },
});
