
import { useStore } from "@/store";
import { computed, defineComponent } from "vue";

export default defineComponent({
  name: "Link",
  props: {
    bold: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const store = useStore();

    return {
      theme: computed(() => store.state.settings.theme),
    };
  },
});
