type TAuthFormReturn = {
  type: "login" | "password" | "";
  message: string;
};

type TRegisterFormReturn = {
  type: "login" | "password" | "confirm" | "";
  message: string;
};

type TPaintingFormReturn = {
  type: "name" | "year" | "";
  message: string;
};

export const authFormValidation = (
  login: string,
  password: string,
): TAuthFormReturn | void => {
  const error: TAuthFormReturn = {
    type: "",
    message: "  ",
  };

  //   
  if (!login) {
    error.type = "login";
    return error;
  }
  if (!password) {
    error.type = "password";
    return error;
  }

  if (error.type) return error;
};

export const registerFormValidation = (
  login: string,
  password: string,
  confirm: string,
): TRegisterFormReturn | void => {
  const error: TRegisterFormReturn = {
    type: "",
    message: "  ",
  };

  //   
  if (!login) {
    error.type = "login";
    return error;
  }
  if (!password) {
    error.type = "password";
    return error;
  }
  if (!confirm) {
    error.type = "confirm";
    return error;
  }

  //  
  if (login.length > 50) {
    error.type = "login";
    error.message = "     50 ";
    return error;
  }
  if (password.length < 8) {
    error.type = "password";
    error.message = "     8 ";
    return error;
  }
  if (password !== confirm) {
    error.type = "confirm";
    error.message = "  ";
    return error;
  }
};

export const paintingFormValidation = (
  name: string,
  year: string,
): TPaintingFormReturn | void => {
  const error: TPaintingFormReturn = {
    type: "",
    message: "  ",
  };

  if (!name) {
    error.type = "name";
    return error;
  }

  if (!year) {
    error.type = "year";
    return error;
  }

  if (name.length < 3) {
    error.type = "name";
    error.message = "     3 ";
    return error;
  }

  if (year.length < 4) {
    error.type = "year";
    error.message = "     4 ";
    return error;
  }
};
