
import { useStore } from "@/store";
import { computed, defineComponent, onMounted, ref, watch } from "vue";
import { useRoute, useRouter } from "vue-router";
import EditIcon from "@/assets/icons/edit_icon.svg";
import ArrowIcon from "@/assets/icons/arrow.svg";
import Tag from "@/ui/Tag.vue";
import CardsGrid from "@/ui/CardsGrid.vue";
import PaintingSliderModal from "@/components/PaintingSliderModal.vue";
import AvatarMock from "@/assets/images/painting-mock.svg";
import bodyLock from "@/utils/bodyLock";
import EditButton from "@/ui/EditButton.vue";
import RemoveButton from "@/ui/RemoveButton.vue";

export default defineComponent({
  name: "ArtistPage",
  components: {
    EditIcon,
    ArrowIcon,
    Tag,
    CardsGrid,
    PaintingSliderModal,
    AvatarMock,
    EditButton,
    RemoveButton,
  },

  setup() {
    const store = useStore();
    const route = useRoute();
    const router = useRouter();
    const { artistId } = route.params;

    const api = process.env.VUE_APP_BASE_URL;

    const isTextShown = ref<boolean>(false);
    //  
    const isPaintingModalOpen = ref<boolean>(false);

    //     
    watch(isPaintingModalOpen, (newValue) => {
      bodyLock(newValue);
    });

    onMounted(() => {
      if (typeof artistId === "string") {
        store.dispatch("fetchArtistById", artistId);
      }
    });

    const onShowButtonClick = () => {
      isTextShown.value = !isTextShown.value;
    };

    const onModalCLose = () => {
      isPaintingModalOpen.value = false;
    };

    const onPaintingClick = (index: number) => {
      store.commit("setActiveSlide", index);
      store.commit(
        "setActivePaintingId",
        store.state.artist.artist?.paintings[index]._id,
      );
      isPaintingModalOpen.value = true;
    };

    const onAddPaintingClick = () => {
      store.commit("setIsAddPaintingModalOpen", true);
    };

    const closeUploadModal = () => {
      store.commit("setIsAddPaintingModalOpen", false);
    };

    const closeEditModal = () => {
      store.commit("setIsEditPaintingModalOpen", false);
    };

    const onEditArtistClick = () => {
      store.commit("setIsEditArtistModalOpen", true);
    };

    const onRemoveClick = () => {
      store.dispatch("tryToDeleteArtis").then(() => {
        router.push({
          name: "home",
        });
      });
    };

    return {
      theme: computed(() => store.state.settings.theme),
      artist: computed(() => store.state.artist.artist),
      onShowButtonClick,
      isTextShown,
      isPaintingModalOpen,
      onPaintingClick,
      onAddPaintingClick,
      closeUploadModal,
      closeEditModal,
      onModalCLose,
      onEditArtistClick,
      onRemoveClick,
      api,
    };
  },
});
