
import { useStore } from "@/store";
import { defineComponent, onMounted, onUnmounted } from "vue";

export default defineComponent({
  name: "DropZone",
  props: {
    onDragEnter: {
      type: Function,
      default: () => {},
    },
    onDragLeave: {
      type: Function,
      default: () => {},
    },
  },
  setup(props) {
    const conditions = ["jpeg", "jpg", "png"];
    const store = useStore();

    let inActiveTimeout: ReturnType<typeof setTimeout>;

    const setActive = () => {
      props.onDragEnter();
      clearTimeout(inActiveTimeout);
    };

    const setInActive = () => {
      inActiveTimeout = setTimeout(() => {
        props.onDragLeave();
      }, 50);
    };

    const events = ["dragenter", "dragover", "dragleave", "drop"];

    const preventDefaults = (e: Event) => {
      e.preventDefault();
    };

    onMounted(() => {
      events.forEach((eventName) => {
        document.body.addEventListener(eventName, preventDefaults);
      });
    });

    onUnmounted(() => {
      events.forEach((eventName) => {
        document.body.removeEventListener(eventName, preventDefaults);
      });
    });

    const onDrop = (e: DragEvent) => {
      const droppedFiles = e.dataTransfer?.files;

      if (droppedFiles) {
        const filteredFiles = [...droppedFiles].filter((item) =>
          conditions.some((condition) => item.type.includes(condition)),
        );

        if (filteredFiles.length === 1) {
          store.commit("setUploadedFiles", filteredFiles[0]);
        }
      }

      setInActive();
    };

    return {
      onDrop,
      setActive,
      setInActive,
    };
  },
});
